import React, { useState } from "react";
import { Navbar, Nav, Container } from "react-bootstrap";
import { Link } from 'react-router-dom';
import bbb from './assignimg/a.png';
import './Header.css';


const Header = () => {
  const [expanded, setExpanded] = useState(false);

  return (
    <Navbar expand="md" bg="primary" variant="dark" expanded={expanded}>
      <Container>
        <Navbar.Brand href="/">
          <img src={bbb} alt="Logo" className="logo" />
        </Navbar.Brand>
        <Navbar.Toggle
          aria-controls="basic-navbar-nav"
          onClick={() => setExpanded(expanded ? false : "expanded")}
        />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <Link to="/Badr"  className="m" style={{color: '#ffffff'}}>معمل البدر</Link>
            <Link to="/R"  className="m" style={{color: '#ffffff'}}>مهمتنا</Link>
            <Link to="/"  className="m" style={{color: '#ffffff'}}>نتائج التحاليل</Link>
            <Link to="/About"  className="m" style={{color: '#ffffff'}}>اتصل بنا</Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default Header;