import {  Link } from "react-router-dom";
import './card.css';

import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
function Item(props) {
  return (
    <div className="ma col-sm-6  col-md-4">
    <Card style={{ width: '18rem',margin:'8px auto'}}>
    <div class="image-container">

      <Card.Img variant="top" src={props.img} />
      </div>
      <Card.Body>
        <Card.Title>{props.name}</Card.Title>
        <Card.Text>
        {props.gf}
        </Card.Text>
      </Card.Body>
      <Button variant="primary"><Link to="/" style={{color: '#ffffff',textDecoration:'none'}}>تواصل معنا</Link></Button>

    </Card>
    </div>
  );
}

export default Item;