import React from 'react';
import Card from 'react-bootstrap/Card';
import hh from './assignimg/fdfd.jpg'
const Abb = () => {
    const phoneNumber = '01117745757';
  
    function handleCallClick() {
      window.location.href = `tel:${phoneNumber}`;
    }
  return (
    <>
      <Card className='bg-primary' >
        <Card.Img variant="top" src={hh} />
        <Card.Body>
          <Card.Text>
            اتصل بنا عبر الهاتف 
          </Card.Text>
        </Card.Body>
        <Card.Header>
        <button  onClick={handleCallClick}>اتصل الآن</button>

        </Card.Header>

      </Card>

      </>
    );
}

export default Abb;
