import React, { useContext, useState } from "react";
import Img from "../img/img.png";
import Attach from "../img/attach.png";
import { AuthContext } from "../context/AuthContext";
import { ChatContext } from "../context/ChatContext";
import {
  arrayUnion,
  doc,
  serverTimestamp,
  Timestamp,
  updateDoc,
} from "firebase/firestore";
import { db, storage } from "../firebase";
import { v4 as uuid } from "uuid";
import { getDownloadURL, ref, uploadBytesResumable } from "firebase/storage";

const Input = () => {
  const [text, setText] = useState("");
  const [pdf, setPdf] = useState(null);
  const [image, setImage] = useState(null);

  const { currentUser } = useContext(AuthContext);
  const { data } = useContext(ChatContext);

  const handleSend = async () => {
    if (pdf) {
      const pdfRef = ref(storage, `pdfs/${uuid()}.pdf`);
      const uploadTask = uploadBytesResumable(pdfRef, pdf);

      uploadTask.on(
        "state_changed",
        null,
        (error) => {
          // Handle unsuccessful uploads here
        },
        () => {
          getDownloadURL(uploadTask.snapshot.ref).then(async (downloadURL) => {
            await updateDoc(doc(db, "chats", data.chatId), {
              messages: arrayUnion({
                id: uuid(),
                text,
                senderId: currentUser.uid,
                date: Timestamp.now(),
                pdf: downloadURL,
              }),
            });
          });
        }
      );
    }

    if (image) {
      const imgRef = ref(storage, `images/${uuid()}.jpg`);
      const uploadTask = uploadBytesResumable(imgRef, image);

      uploadTask.on(
        "state_changed",
        null,
        (error) => {
          // Handle unsuccessful uploads here
        },
        () => {
          getDownloadURL(uploadTask.snapshot.ref).then(async (downloadURL) => {
            await updateDoc(doc(db, "chats", data.chatId), {
              messages: arrayUnion({
                id: uuid(),
                text,
                senderId: currentUser.uid,
                date: Timestamp.now(),
                img: downloadURL,
              }),
            });
          });
        }
      );
    }

    if (!pdf && !image) {
      await updateDoc(doc(db, "chats", data.chatId), {
        messages: arrayUnion({
          id: uuid(),
          text,
          senderId: currentUser.uid,
          date: Timestamp.now(),
        }),
      });
    }

    await updateDoc(doc(db, "userChats", currentUser.uid), {
      [data.chatId + ".lastMessage"]: {
      text,
      },
      [data.chatId + ".date"]: serverTimestamp(),
      });
      
      await updateDoc(doc(db, "userChats", data.user.uid), {
        [data.chatId + ".lastMessage"]: {
          text,
        },
        [data.chatId + ".date"]: serverTimestamp(),
      });
      
      setText("");
      setPdf(null);
      setImage(null);
      };
      
      return (
      <div className="input">
      <input
      type="text"
      placeholder="Type something..."
      onChange={(e) => setText(e.target.value)}
      value={text}
      />
      <div className="send">
      <label htmlFor="pdf">
      <img src={Attach} alt="" />
      </label>
      <input
      type="file"
      style={{ display: "none" }}
      id="pdf"
      accept=".pdf"
      onChange={(e) => setPdf(e.target.files[0])}
      />
      <label htmlFor="image">
      <img src={Img} alt="" />
      </label>
      <input
      type="file"
      style={{ display: "none" }}
      id="image"
      accept="image/*"
      onChange={(e) => setImage(e.target.files[0])}
      />
      <button onClick={handleSend}>Send</button>
      </div>
      </div>
      );
      };
      
      export default Input;