import React from 'react';
import gf from './assignimg/pngwing.com.png'

import cc from './assignimg/cc.jpg'
const Bin = () => {
    return (
        <div>
            <div className='container bv' style={{margin:'20px'}}>
        <img src={gf} alt='dd' />
        <div>
          <h3>عن معمل البدر  </h3>
          <p>لدينا أكفأ الأطباء يعملون على أحدث الأجهزة التقنية الطبية</p>
        </div>
      </div>
      <div className='container bv' style={{margin:'20px'}}>
        <img src={cc} alt='dd' />
        <div>
          <h3>مهمتنا </h3>
          <p>توفير خدمات معملية موثوق بها ومناسبة في الوقت والتكاليف للمرضى وأطبائنا ومقدمي الرعاية الصحية.</p>
        </div>
      </div>
      <div className='container bv' style={{margin:'20px'}}>
        <img src={cc} alt='dd' />
        <div>
          <h3>• لماذا تختار معامل البدر ؟</h3>
          <p>لنا ريادة في اعتماد معايير إدارة الجودة الشاملة في الصناعة الإقليمية للمعامل ومنذ ذلك الوقت ، قدمنا عدداً من مقاييس ضمان الجودة لضمان أعلى درجة من الدقة في إجراءات الفحص لدينا</p>
        </div>
      </div>
        </div>
    );
}

export default Bin;
