
import ezam from './assignimg/ezam.JPG';
import tfl from './assignimg/tfl.JPG';
import xc from './assignimg/xc.JPG';
import sl from './assignimg/sا.jpg';
import hml from './assignimg/hml.JPG';
import sss from './assignimg/sss.jpg';
import './card.css';




import Item from './Item'
const dat =["تحاليل ما قبل العمليات","تحاليل تساقط الشعر","تحاليل متابعة الحمل","تحاليل متابعة الاطفال","تحاليل العظام", "تحاليل الدم"]
const title =["جميع التحاليل اللازمة قبل العمليات باحدث التقنيات والاجهزة","تحاليل ضعف وتساقط الشعر وبطئ النمو وعلاج الزلح","التحاليل اللازمة للاطمئنان علي الجنين والام وعمل الفحوصات اللازمة"," اطمن علي طفلك وقم باجراء تحاليل النمو والدم والمناعة وغيرها","جميع التحاليل الشاملة التي يطلبها أطباء العظام بالتقنيات الحديثة ", "       هنقدر نقولك كل التفاصيل اللازمة للاهتمام بصحتك"]
const Card = () => {
  return (
    <div className="row">
<Item
name={dat[0]}
img={xc}
gf={title[0]}

/>
<Item 
name={dat[1]}
img={sl}
gf={title[1]}

/>
<Item
name={dat[2]}
img={hml} 
gf={title[2]}

/>
<Item 
name={dat[3]}
img={tfl}
gf={title[3]}

/>
<Item 
name={dat[4]}
img={ezam}
gf={title[4]}

/>
<Item 
name={dat[5]}
img={sss}
gf={title[5]}

/>
    </div>
  )
}

export default Card